import { Row, Col } from 'antd';
import styled from 'styled-components';
import StyledLink from './styled-link';
import { GatsbyImage } from 'gatsby-plugin-image';


const titleStyle = {
    textAlign: "center",
    color: '#ffffff',
    margin: "30px 0px"
  }
  
  const itemContainerStyle = {
    color: "#013b89",
    background: "#013b89",
    padding: "40px",
    top: "0",
    transition: "top ease 0.5s"
  }
  
  const itemStyle = {
    background: "#ffffff",
    textAlign: "center",
    padding: "40px 10px",
    height: "calc(25vw - 80px)",
    margin: "auto",
    color: "#013b89",
    borderRadius: "50%",
    position: "relative",
    top: "50%",
    transform: "translateY(-50%)",
    padding: "50px",
    fontSize: "17px",
  }
  
  const AboutGeneral = () => {
      return (
          <div style={{ margin: "0px 15vw", paddingTop: "60px" }}>
            <Row>
              <Col span={6} push={18}>
                <img src="/media/logo.png" style={{objectFit: "cover", width: "15vw", height: "15vw", borderRadius: "50%", margin: "auto", verticalAlign: "middle", position: "relative", top: "50%", transform: "translateY(-50%)" }}/>
              </Col>
           <Col span={18} pull={6} style={{ overflow: "hidden", padding: "0px"}}>
           <br/>
            <h1 style={{ color: "#013b89", textAlign: "center" }}>About Us</h1>
            <br/>
            <h3>About Pre-Law Shadowers</h3>
            <br/>
            <p> Pre-Law Shadowers is a student-led organization dedicated to providing pre-law students across universities in Canada with virtual shadowing opportunities. Our goal is to help young students engage and learn more about the legal careers they plan to pursue. It can be difficult to find learning opportunities during the pandemic, so we started this organization in the hopes that it can be both accessible and educationally fulfilling for students.</p>
            <br/><br/>
            <p>We have created a great space where students can network with each other, thus creating lasting friendships and a welcoming community. They can also network with legal professionals in the field varying from lawyers, paralegals, law clerks, judges and even current law students, in order to learn more about the futures they aspire to build for themselves.</p>
            <br/><br/>
            <p>Through Pre-Law Shadowers, students can also gain insight and exposure into the legal field with exciting opportunities for volunteer programs and internships. Overall, this is an amazing platform that every pre-law student should be a part of!</p>
            <br/>
           </Col>
            </Row>
          </div> 
      );
  };

